.inner {
  display: block;
  width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.inner .row {
  display: flex;
  font-size: 14px;
}
.inner .row:nth-child(1) {
  background-color: #fbfcfd;
  color: #9b9b9b;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 20px;
}
.inner .row:nth-child(2) ul {
  display: flex;
  background-color: bisque;
}
.inner .row:nth-child(2) ul li {
  font-size: 14px;
  margin-right: 20px;
  position: relative;
  cursor: pointer;
}
.inner .row:nth-child(2) ul li::after {
  content: '';
  position: absolute;
  display: block;
  width: 1px;
  height: 90%;
  background-color: var(--color-light-grey);
  right: -10px;
  top: 0;
  bottom: 0;
}
.inner .row:nth-child(2) ul li:last-child:after {
  display: none;
}

.inner .infoRow {
  display: flex;
  gap: 100px;
}
