.col {
  border: 1px solid red;
}
.title {
  font-weight: 700;
  font-size: 14px;
  margin: 5px 0;
}
.text {
}

ul.contents {
  font-size: 12px;
}
ul.contents_grid {
  font-size: 12px;
  display: grid;
  grid-template-columns: repeat(2, minmax(10px, 1fr));

  width: 500px;
}

li.content {
  background-color: yellow;
  margin-bottom: 7px;
}
.contents_grid .content span {
  background-color: turquoise;
  position: relative;
}

.contents_grid .content:nth-child(2n-1) span::after {
  content: '';
  display: block;
  position: absolute;
  right: -5px;
  bottom: 0;
  width: 1px;
  height: 100%;
  background-color: var(--color-grey);
}
